export const getSkinTypeText = (skinType: string) => {
  const texts: Record<string, string> = {
    "combination": "Mixte",
    "dry": "Sche",
    "oily": "Grasse",
    "normal": "Normale"
  };
  return texts[skinType] || texts["normal"];
};

export const getSkinStateText = (state: string | null) => {
  if (!state || state === "normal") return null;

  const texts: Record<string, string> = {
    "sensitive": "Sensible"
  };
  return texts[state];
};

export const getSkinStateDetails = (state: string | null): SkinStateDetail | null => {
  if (!state || state === "normal") return null;

  const details: Record<string, SkinStateDetail> = {
    "sensitive": {
      title: "tat Sensible",
      description: "Ta peau prsente une ractivit accrue aux stimuli externes, ncessitant des soins particuliers.",
      characteristics: [
        "Ractivit cutane aux stimuli externes",
        "Rougeurs frquentes ou permanentes",
        "Sensations d'inconfort (picotements, tiraillements)",
        "Intolrance  certains ingrdients cosmtiques"
      ],
      recommendations: [
        "Privilgier les produits hypoallergniques",
        "Tester toujours avant utilisation",
        "viter les parfums et alcools",
        "Utiliser des produits apaisants",
        "Protger des agressions extrieures"
      ]
    }
  };

  return details[state] || null;
};

