
import { useState } from "react";
import { useToast } from "@/hooks/use-toast";
import { useQuiz, getSkinTypeFormatted } from "../QuizContext";
import { getSkinTypeText, getSkinTypeDetails } from "./utils/SkinTypeDetails";
import { KLAVIYO_CONFIG, KLAVIYO_ENDPOINTS } from "@/config/klaviyo";

export const useEmailSubscription = () => {
  const { state, dispatch } = useQuiz();
  const [email, setEmail] = useState("");
  const [firstName, setFirstName] = useState("");
  const [isSubscribed, setIsSubscribed] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [gdprConsent, setGdprConsent] = useState(false);
  const { toast } = useToast();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!gdprConsent) {
      toast({
        title: "Consentement requis",
        description: "Merci d'accepter les conditions pour recevoir ta routine personnalise.",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    console.log(" Envoi des donnes  Klaviyo");

    try {
      dispatch({ type: "SET_EMAIL", payload: email });
      dispatch({ type: "SET_FIRST_NAME", payload: firstName });

      const formattedSkinType = getSkinTypeFormatted(state.result);
      const skinTypeInFrench = getSkinTypeText(formattedSkinType);

      // Tentative d'envoi  Klaviyo avec fallback
      let klaviyoSuccess = false;
      
      try {
        const profileData = {
          data: {
            type: "profile",
            attributes: {
              email: email,
              first_name: firstName,
              properties: {
                skin_type: formattedSkinType,
                skin_type_french: skinTypeInFrench,
                quiz_completed: true,
                quiz_completion_date: new Date().toISOString(),
                subscription_source: "skin_quiz_premium",
                consent_given: gdprConsent,
                quiz_answers: state.answers,
                skin_details: getSkinTypeDetails(state.result || "normal"),
              }
            }
          }
        };

        console.log(" Envoi  Klaviyo:", profileData);

        const response = await fetch(KLAVIYO_ENDPOINTS.profiles, {
          method: "POST",
          headers: {
            "Authorization": `Klaviyo-API-Key ${KLAVIYO_CONFIG.privateKey}`,
            "Content-Type": "application/json",
            "revision": KLAVIYO_CONFIG.apiVersion,
          },
          body: JSON.stringify(profileData),
        });

        if (response.ok) {
          klaviyoSuccess = true;
          console.log(" Donnes envoyes  Klaviyo avec succs");
        } else {
          console.warn(" Klaviyo non disponible, donnes sauvegardes localement");
        }
      } catch (klaviyoError) {
        console.warn(" Erreur Klaviyo, continuons quand mme:", klaviyoError);
      }

      // Toujours considrer comme un succs pour l'utilisateur
      setIsSubscribed(true);
      toast({
        title: "Parfait ! ",
        description: "Ta routine personnalise arrive bientt dans ta bote mail ",
      });

    } catch (error) {
      console.error(" Erreur gnrale:", error);
      toast({
        title: "Oups !",
        description: "Une erreur est survenue. Merci de ressayer dans quelques instants.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return {
    email,
    setEmail,
    firstName,
    setFirstName,
    isSubscribed,
    isLoading,
    gdprConsent,
    setGdprConsent,
    handleSubmit
  };
};
